.star {
  position: absolute;
  background-color: transparent;
  box-shadow: 0px 0px 20px #f2f2f2;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  opacity: 0;
  -webkit-animation: twinkle infinite ease-in-out; /* Chrome, Safari 5 */
  -moz-animation: twinkle infinite ease-in-out; /* Firefox 5-15 */
  -o-animation: twinkle infinite ease-in-out; /* Opera 12+ */
  animation: twinkle infinite ease-in-out; /* Chrome, Firefox 16+, IE 10+, Safari 5 */
}

.star1 {
  top: 30%;
  left: 50%;
  animation-duration: 2s;
}

.star2 {
  top: 20%;
  left: 80%;
  animation-duration: 1.5s;
}

.star3 {
  animation-delay: 1s;
  top: 80%;
  right: 80%;
  animation-duration: 1s;
}

.star4 {
  top: 80%;
  right: 30%;
  animation-duration: 2.5s;
}

.star5 {
  top: 15%;
  right: 90%;
  height: 5px;
  width: 5px;
  animation-duration: 3s;
}

@-webkit-keyframes twinkle {
  0% {
    opacity: 0;
    transform: scale(0.95);
    background-color: rgba(255, 255, 255, 0.2);
  }

  50% {
    opacity: 1;
    transform: scale(1.1);
    background-color: rgba(255, 255, 255, 1);
  }
  100% {
    opacity: 0;
    transform: scale(0.95);
    background-color: rgba(255, 255, 255, 0.2);
  }
}

