@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  font-family: "Source Sans Pro", sans-serif;
}

.main {
  min-height: 100vh;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: #f2f2f2;
  position: relative;
  font-family: "Poppins", sans-serif;
}
.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
}
.main-content-text {
  z-index: 2;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.main-content-h1 {
  font-size: 6rem;
  font-weight: 700;
  overflow: hidden;
  z-index: 2;
}
.dark-h1 {
  color: #494949;
}
.h1-span {
  position: relative;
}
.h1-span::after {
  content: "";
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  border-left: 5px solid #f2f2f2;
  background: #1b1b1b;
  margin-left: 115px;
  animation: typing 2.5s steps(13) infinite;
  /* QQ Browser Support */
  -webkit-animation: typing 2.5s steps(13) infinite;
}
@keyframes typing {
  100% {
    left: 100%;
  }
}
.main-period {
  font-size: 2rem;
  margin-left: 1rem;
  color: #742fc2;
  margin-bottom: 2rem;
}
.main-svg {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.coding-svg {
  height: 50vh;
  width: 100%;
}
.stars-container {
  position: absolute;
  height: 100%;
  width: 100%;
}
.main-background-text {
  position: absolute;
  top: 10vh;
  font-size: 19rem;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #383838;
  color: transparent;
  z-index: 1;
}
.main-btn {
  background: #742fc2;
  font-size: 1.3rem;
  color: #f2f2f2;
  border: none;
  padding: 0.8rem 2rem;
  font-weight: 700;
  box-shadow: 0 0 20px #742fc2;
  cursor: pointer;
  border-radius: 5px;
}

/***** Media queires *****/
@media screen and (max-width: 1280px) {
  .main-content-h1 {
    font-size: 5rem;
  }
  .main-background-text {
    font-size: 15rem;
  }
}
@media screen and (max-width: 1024px) {
  .main-content {
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    width: 100%;
  }
  .main-content-text {
    text-align: center;
    align-items: center;
    height: 40vh;
    width: 90%;
    margin-top: 1rem;
  }
  .main-content-h1 {
    font-size: 6rem;
    margin-bottom: 1rem;
  }
  .main-svg {
    width: 90%;
    align-items: center;
    justify-content: center;
  }
  .coding-svg {
    height: 40vh;
    transform: translateX(-10px);
  }
  .main-background-text {
    font-size: 13.5rem;
    top: 50%;
    transform: translateY(-50%);
  }
}
@media screen and (max-width: 768px) {
  .main-content {
    padding-top: 5vh;
  }
  .main-content-text {
    height: 40vh;
    width: 90%;
    justify-content: flex-start;
    margin-bottom: 2rem;
  }
  .main-content-h1 {
    font-size: 5rem;
  }
  .coding-svg {
    height: 45vh;
  }
  .main-btn {
    font-size: 1.8rem;
  }
  .main-background-text {
    display: none;
  }
}
@media screen and (max-width: 540px) {
  .main-content-text {
    margin-bottom: 0;
  }
  .main-content-h1 {
    font-size: 3rem;
  }
  .main-content-images {
    z-index: 2;
    margin: 0;
  }
  .main-content-images img {
    height: 70px;
  }
  .h1-span::after {
    margin-left: 70px;
  }
  .main-period {
    font-size: 1.2rem;
  }
  .main-btn {
    font-size: 1.3rem;
    padding: 0.8rem 4rem;
  }
}
@media screen and (max-width: 500px) {
  .main-content {
    justify-content: center;
  }
  .main-content-text {
    height: 30vh;
    margin: 0;
    justify-content: center;
    width: 100%;
    padding: 0 0.5rem;
  }
  .h1-span::after {
    margin-left: 60px;
    height: 100%;
  }
  .coding-svg {
    height: 50vh;
    transform: scale(1.3);
  }
  .main-btn {
    display: none;
  }
}
@media screen and (max-width: 400px) {
  .main-content-h1 {
    font-size: 2.5rem;
  }
  .h1-span::after {
    margin-left: 55px;
  }
  .main-period {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 359px) {
  .main-content-h1 {
    font-size: 2rem;
  }
}
@media screen and (max-width: 300px) {
  .main-content-text {
    height: 20vh;
  }
  .main-content-h1 {
    font-size: 1.8rem;
  }
}

.star {
  position: absolute;
  background-color: transparent;
  box-shadow: 0px 0px 20px #f2f2f2;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  opacity: 0; /* Chrome, Safari 5 */ /* Firefox 5-15 */ /* Opera 12+ */
  animation: twinkle infinite ease-in-out; /* Chrome, Firefox 16+, IE 10+, Safari 5 */
}

.star1 {
  top: 30%;
  left: 50%;
  animation-duration: 2s;
}

.star2 {
  top: 20%;
  left: 80%;
  animation-duration: 1.5s;
}

.star3 {
  animation-delay: 1s;
  top: 80%;
  right: 80%;
  animation-duration: 1s;
}

.star4 {
  top: 80%;
  right: 30%;
  animation-duration: 2.5s;
}

.star5 {
  top: 15%;
  right: 90%;
  height: 5px;
  width: 5px;
  animation-duration: 3s;
}

@keyframes twinkle {
  0% {
    opacity: 0;
    transform: scale(0.95);
    background-color: rgba(255, 255, 255, 0.2);
  }

  50% {
    opacity: 1;
    transform: scale(1.1);
    background-color: rgba(255, 255, 255, 1);
  }
  100% {
    opacity: 0;
    transform: scale(0.95);
    background-color: rgba(255, 255, 255, 0.2);
  }
}

.nav {
  height: 7vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  color: #f2f2f2;
  z-index: 4;
  background: #181717;
  font-family: "Poppins", sans-serif;
}
.nav-logo {
  margin: 0 2rem;
  object-fit: contain;
  height: 50px;
  width: 50px;
}
.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-link {
  list-style: none;
  padding: 0.5rem;
  font-size: 1.2rem;
  position: relative;
  cursor: pointer;
  margin: 0 2rem;
}
.nav-link .is-active {
  border-bottom: 5px solid #742fc2;
}
.nav-link a {
  color: #f2f2f2;
  text-decoration: none;
}
.burger {
  display: none;
}
.burger div {
  height: 3px;
  width: 25px;
  margin: 5px;
  background: #f2f2f2;
  transition: transform 0.2s ease-in-out;
}
.line-1.toggle {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.line-2.toggle {
  opacity: 0;
}
.line-3.toggle {
  transform: rotate(45deg) translate(-5px, -6px);
}
/***** Media Queires *****/
@media screen and (max-width: 1280px) {
  .nav-logo {
    height: 40px;
    width: 40px;
  }
}
@media screen and (max-width: 1024px) {
  .nav-link {
    padding: 1rem;
    font-size: 1rem;
    margin: 0 1.5rem;
  }
  .nav-logo {
    height: 50px;
    width: 50px;
  }
}
@media screen and (max-width: 768px) {
  .nav-link {
    margin: 0 0.5rem;
  }
}
@media screen and (max-width: 540px) {
  .burger {
    display: inline-block;
    margin-right: 2rem;
  }
  .nav-links {
    position: absolute;
    height: 40vh;
    top: 7vh;
    right: 0;
    width: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-end;
    background: #1b1b1b;
    box-shadow: 0 10px 20px #000;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
    pointer-events: none;
  }
  .nav-links.active {
    opacity: 1;
    transition: opacity 0.2s ease-in-out;
    pointer-events: all;
  }
  .nav-link {
    font-size: 1.2rem;
  }
  .nav-logo {
    height: 40px;
    width: 40px;
  }
}
@media screen and (max-width: 360px) {
  .nav-logo {
    height: 30px;
    width: 30px;
  }
}

.aboutme {
  height: 100vh;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: #f2f2f2;
  position: relative;
  overflow-x: hidden;
}
.aboutme-title {
  font-size: 4rem;
  color: #742fc2;
  font-weight: 700;
  align-self: center;
  letter-spacing: 3px;
}
.aboutme-content {
  width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: start;
  justify-content: start;
  transform: translateY(-10px);
}
.aboutme-content a {
  text-decoration: none;
  color: #f2f2f2;
}
.aboutme-subtitle {
  font-size: 2.8rem;
  color: #742fc2;
  font-weight: 700;
  align-self: flex-start;
}
.aboutme-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  font-size: 1.3rem;
  line-height: 1.5;
  position: relative;
  font-weight: 400;
  flex: 1 1;
  padding: 0 2rem;
}
.aboutme-info p {
  margin: 0.8rem 0;
}
.secondary-btn {
  margin-top: 0.8rem;
}
.name-span {
  color: #742fc2;
  font-weight: 700;
}
.skills-section {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex: 1 1;
  padding: 0 2rem;
}
/***** Media queries *****/
@media screen and (max-width: 1280px) {
  .aboutme-content {
    width: 100%;
  }
  .aboutme-title {
    font-size: 3rem;
    padding-top: 7vh;
  }
  .aboutme p {
    font-size: 1.25rem;
  }
}
@media screen and (max-width: 1024px) {
  .aboutme {
    height: auto;
  }
  .aboutme-title {
    margin: 1rem 0 2rem 0;
  }
}
@media screen and (max-width: 768px) {
  .aboutme {
    justify-content: space-between;
  }
  .aboutme-content {
    grid-template-columns: 1fr;
  }
  .aboutme-subtitle {
    font-size: 2rem;
  }
  .secondary-btn {
    font-size: 1rem;
  }
  .skills-section {
    margin: 4rem 0;
  }
}
@media screen and (max-width: 540px) {
  .aboutme-title {
    font-size: 2.5rem;
  }
  .aboutme-subtitle {
    font-size: 1.7rem;
  }
  .aboutme-info p {
    font-size: 1rem;
  }
}
@media screen and (max-width: 500px) {
  .secondary-btn {
    display: block;
