  justify-content: space-evenly;
  align-items: flex-start;
  margin: 0 1rem;
  height: 350px;
}
.project-head {
  font-size: 2.5rem;
  color: #742fc2;
  font-weight: 700;
}
.title-technologies {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.title-technology:nth-child(1) {
  margin-left: 0;
}
.title-technology {
  padding: 0.2rem 0.5rem;
  border: 2px solid #742fc2;
  border-radius: 5px;
  font-weight: 700;
  margin: 0 0.5rem;
}
.project-description {
  font-size: 1.5rem;
  font-size: 1.3rem;
  line-height: 1.5;
}
.title-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.title-link:nth-child(1) {
  margin-left: 0;
}
.title-link {
  margin: 0.5rem;
  position: relative;
  font-weight: 700;
  padding: 0.2rem 0.5rem;
  letter-spacing: 1px;
}
.title-link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  height: 3px;
  width: 100%;
  background: grey;
}
.title-link:hover::after {
  background: #742fc2;
}
.title-links a {
  text-decoration: none;
  color: #f2f2f2;
}

/***** Media queires *****/
@media screen and (max-width: 1280px) {
  .project-container-content {
    width: 100%;
  }
  .project-title {
    font-size: 3rem;
  }
  .projects-h1 {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1024px) {
  .project-container img {
    height: 200px;
    margin: 0 1rem;
  }
}
@media screen and (max-width: 768px) {
  .projects-h1 {
    width: 95%;
  }
  .project-container {
    height: 650px;
  }
  .project-container-content {
    flex-direction: column;
  }
  .left-image {
    flex-direction: column-reverse;
  }
  .project-container img {
    height: 300px;
  }
  .project-header {
    height: 300px;
    width: 90%;
  }
}
@media screen and (max-width: 540px) {
  .project-title {
    font-size: 2.5rem;
  }
  .projects-h1,
  .project-description {
    font-size: 1rem;
  }
  .project-container {
    height: 470px;
    width: 100%;
  }
  .project-container img {
    height: 230px;
  }
  .project-head {
    font-size: 1.7rem;
  }
}
@media screen and (max-width: 500px) {
  .project-container {
    width: 370px;
    padding: 0 1rem;
  }
  .project-header {
    width: 100%;
  }
  .project-container img {
    height: 170px;
    margin: 1rem 0;
  }
  .title-technology {
    margin: 0.2rem;
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 400px) {
  .project-container {
    width: 350px;
  }
}
@media screen and (max-width: 359px) {
  .project-container {
    width: auto;
  }
  .project-header {
    align-items: center;
    text-align: center;
    height: 350px;
  }
  .title-technologies {
    justify-content: center;
  }
  .project-container img {
    height: 150px;
  }
}
@media screen and (max-width: 300px) {
  .project-title {
    font-size: 2rem;
  }
  .project-container {
    height: 520px;
  }
  .project-container img {
    height: 130px;
  }
}

.footer {
  background: #1b1b1b;
  height: 15vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.footer-socials {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 15%;
}
.footer a {
  color: #f2f2f2;
  font-weight: 700;
}
.footer-socials a {
  color: #f2f2f2;
  transition: color 0.2s ease-in-out;
  padding: 0.5rem;
}
.footer-socials a:hover {
  color: #742fc2;
}
.footer-socials i {
  font-size: 2rem;
}
.footer-copyright {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(199, 199, 199);
}
.copyright-i {
  margin-right: 0.3rem;
}

/***** Media queires *****/
@media screen and (max-width: 1024px) {
  .footer-socials {
    width: 30%;
  }
}
@media screen and (max-width: 540px) {
  .footer-copyright p {
    font-size: 0.8rem;
  }
  .copyright-i {
    font-size: 0.8rem;
  }
  .footer-socials i {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 500px) {
  .footer-socials {
    width: 60%;
  }
}

